/* -------------------------------------------------------------------
 * ## header logo
 * ------------------------------------------------------------------- */
.header__logo {
  display: inline-block;
  margin: 3px 0 0 0;
  padding: 0;
  z-index: 501;
  position: absolute;
  top: 50%;
  left: 40px;
}

.header__logo a {
  display: block;
