footer h4 {
  font-weight: 700;
  font-size: 1.4rem;
  line-height: 1.286;
  text-transform: uppercase;
  letter-spacing: .25rem;
  padding-bottom: 4.2rem;
  margin-top: 0;
  position: relative;
}

footer h4::after {
  content: "";
  display: block;
  height: 1px;
  width: 120px;
  background-color: rgba(255, 255, 255, 0.1);
  position: absolute;
  left: 0;
  bottom: 0;
}

footer [class*="s-footer__"] > .row [class*="col-"]:first-child {
