}

footer [class*="s-footer__"] > .row [class*="col-"]:last-child {
  padding-left: 45px;
}

.s-footer__main {
  margin-bottom: 3rem;
}

/* -------------------------------------------------------------------
 * ## subscribe form
 * ------------------------------------------------------------------- */
.s-footer__subscribe .subscribe-form {
  margin: 0;
  padding: 0;
  position: relative;
}

.s-footer__subscribe #mc-form {
  width: 100%;
}

.s-footer__subscribe #mc-form input[type="email"] {
  width: 100%;
  height: 5.4rem;
  font-size: 1.5rem;
  padding: 1.2rem 100px 1.2rem 60px;
  background: url(../img/icons/icon-mail.svg) 20px center no-repeat, rgba(0, 0, 0, 0.25);
  background-size: 24px 16px, auto;
  margin-bottom: 1.8rem;
  color: white;
  border: none;
}

.s-footer__subscribe #mc-form input[type="email"]::-webkit-input-placeholder {

  /* WebKit, Blink, Edge */
  color: #FFFFFF;
}

.s-footer__subscribe #mc-form input[type="email"]:-moz-placeholder {

  /* Mozilla Firefox 4 to 18 */
  color: #FFFFFF;
  opacity: 1;
}

.s-footer__subscribe #mc-form input[type="email"]::-moz-placeholder {

  /* Mozilla Firefox 19+ */
  color: #FFFFFF;
  opacity: 1;
}

.s-footer__subscribe #mc-form input[type="email"]:-ms-input-placeholder {

  /* Internet Explorer 10-11 */
  color: #FFFFFF;
}

.s-footer__subscribe #mc-form input[type="email"]::-ms-input-placeholder {

  /* Microsoft Edge */
  color: #FFFFFF;
}

.s-footer__subscribe #mc-form input[type="email"]::placeholder {

  /* Most modern browsers support this now. */
  color: #FFFFFF;
}

.s-footer__subscribe #mc-form input[type="email"].placeholder {
  color: #FFFFFF !important;
}

.s-footer__subscribe #mc-form input[type=submit] {
  position: absolute;
  top: 0;
  right: 0;
  color: #FFFFFF;
  background: #000000;
  border-color: #000000;
  margin-right: 0;
  padding: 0 20px;
  height: 5.4rem;
  line-height: 5rem;
}

.s-footer__subscribe #mc-form label {
  color: #FFFFFF;
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.3rem;
  padding: 0 2rem;
}

.s-footer__subscribe #mc-form label i {
  padding-right: 6px;
}

/* -------------------------------------------------------------------
 * ## copyright
 * ------------------------------------------------------------------- */
.s-footer__bottom .s-footer__copyright span {
  display: inline-block;
}

.s-footer__bottom .s-footer__copyright span::after {
  content: "|";
  display: inline-block;
  padding: 0 1rem 0 1.2rem;
  color: rgba(255, 255, 255, 0.1);
}

.s-footer__bottom .s-footer__copyright span:last-child::after {
  display: none;
}

/* ------------------------------------------------------------------- 
 * ## footer social
 * ------------------------------------------------------------------- */
.footer-social {
  list-style: none;
  display: inline-block;
  margin: -0.6rem 0 0 0;
  font-size: 3rem;
  line-height: 1.5;
}

.footer-social li {
  margin-right: 2rem;
  padding-left: 0;
  display: inline-block;
}

.footer-social li a {
  color: #FFFFFF;
}

.footer-social li a:hover, .footer-social li a:focus {
  color: #FFFFFF;
}

.footer-social li:last-child {
  margin: 0;
}

/* ------------------------------------------------------------------- 
 * ## go to top
 * ------------------------------------------------------------------- */
.go-top {
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 600;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all 0.8s ease-in-out;
  transition: all 0.8s ease-in-out;
  -webkit-transform: translate3d(0, 200%, 0);
  -ms-transform: translate3d(0, 200%, 0);
  transform: translate3d(0, 200%, 0);
}

.go-top a, .go-top a:visited {
  text-decoration: none;
  border: 0 none;
  display: block;
  height: 6rem;
  width: 6rem;
  line-height: 6rem;
  text-align: center;
  background-image: url(../img/icons/icon-arrow-up.svg);
  background-color: #000000;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 7px 16px;
  color: rgba(255, 255, 255, 0.5);
  text-align: center;
  text-transform: uppercase;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.go-top a:hover, .go-top a:focus, .go-top a:visited:hover, .go-top a:visited:focus {
  height: 6.6rem;
}

.go-top.link-is-visible {
  opacity: 1;
  visibility: visible;
  -webkit-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

