  list-style: none;
}

.commentlist {
  margin: 6rem 0 5.4rem;
  padding: 0;
}

.commentlist > .comment {
  position: relative;
  list-style: none;
  margin: 0;
  padding: 1.8rem 0 0 0;
  padding-left: 14%;
}

.comment__avatar {
  position: absolute;
  left: 0;
  display: block;
}

.comment__avatar img {
  height: 6.6rem;
  width: 6.6rem;
  border-radius: 100%;
  vertical-align: bottom;
}

.comment__info {
  position: relative;
}

.comment__info .comment__author {
  font-weight: 700;
  font-size: 1.9rem;
  line-height: 1.579;
  font-style: normal;
  color: #000000;
}

.comment__meta {
  font-family: "Nunito Sans", sans-serif;
  font-style: normal;
  display: block;
  color: rgba(0, 0, 0, 0.5);
  text-align: right;
  width: 150px;
  position: absolute;
  top: 0;
  right: 0;
}

.comment__meta .comment__time {
  font-size: 1.4rem;
  text-transform: uppercase;
  letter-spacing: .1rem;
  color: rgba(0, 0, 0, 0.5);
}

.comment__meta .comment__reply a {
  font-size: 1.3rem;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: .2rem;
  color: #000000;
}

.comment__text {
  clear: both;
  margin: 1.5rem 0 0 0;
  padding: 0 175px 0 0;
}

.comment ul.children {
  margin: 0;
  padding: 0;
}

.comment ul.children li {
  padding-left: 5%;
  margin-top: calc(3rem + 1.8rem);
  border-left: 1px solid rgba(0, 0, 0, 0.15);
}


/* ------------------------------------------------------------------- 
 * responsive:
 * comments
 * ------------------------------------------------------------------- */
@media only screen and (max-width:1000px) {
  #comments, .comments-wrap .comment-respond {
    max-width: 750px;
  }

  .comments-wrap {
    margin-top: 6rem;
  }

  .comment__avatar img {
    height: 6rem;
    width: 6rem;
  }

  .comment__meta {
    text-align: left;
    width: auto;
    position: static;
  }

  .comment__meta .comment__time, .comment__meta .comment__reply {
    display: inline-block;
  }

  .comment__meta .comment__time {
    margin-right: 12px;
  }

  .comment__text {
    padding: 0;
  }

  .comments-wrap .comment-respond {
    margin-top: 4.5rem;
  }

}

@media only screen and (max-width:800px) {
  .comment__avatar img {
    margin-top: 0;
    height: 5.1rem;
    width: 5.1rem;
  }

}

@media only screen and (max-width:600px) {
  .comments-wrap h3 {
    font-size: 2.8rem;
  }

  .comments-wrap h3 span {
    font-size: 1.6rem;
  }

  .commentlist > .comment {
    padding-top: 0;
    padding-left: 0;
  }

  .comment__avatar {
    display: none;
  }

  .comment ul.children li {
    margin-top: calc(3rem + 0rem);
  }

}

@media only screen and (max-width:500px) {
  .comment ul.children li {
    border-left: none;
  }

}

@media only screen and (max-width:400px) {
  .comments-wrap {
    margin-top: 3rem;
  }

  .comment-respond form {
    padding-top: 3rem;
  }

}


/* ===================================================================
 * # page styles and page components
 *
 * ------------------------------------------------------------------- */
.s-content--narrow h4.remove-top-margin {
  margin-top: 0;
}

.s-content--narrow h4.quarter-top-margin {
  margin-top: 1.5rem;
}

.s-content--narrow h4.half-top-margin {
  margin-top: 3rem;
}

/* ------------------------------------------------------------------- 
 * ## contact page
 * ------------------------------------------------------------------- */

/* map
 * ----------------------------------------------- */
#map-wrap {
  background: #FFFFFF;
  z-index: 500;
  height: 420px;
  margin: 6rem 0 3rem 0;
}

#map-wrap .gm-style img {
  max-width: none;
}

#map-wrap .gm-style label {
  width: auto;
  display: inline;
}

#map-wrap #map-container {
  height: 100%;
  width: 100%;
  background: #DDDDDD;
  position: relative;
}

#map-wrap #map-zoom-in, #map-wrap #map-zoom-out {
  height: 3.2rem;
  width: 3.2rem;
  cursor: pointer;
  margin-right: 4rem;
  color: white;
  background-color: #000000;
  background-repeat: no-repeat;
  background-position: center center;
  display: none;
}

#map-wrap #map-zoom-in {
  margin-bottom: 2px;
  margin-top: 30px;
}

#map-wrap #map-zoom-in {
  background-image: url(../img/icons/icon-plus.svg);
  background-size: 10px 10px;
}

#map-wrap #map-zoom-out {
  background-image: url(../img/icons/icon-minus.svg);
  background-size: 10px 10px;
}

#map-wrap #map-zoom-in:hover, #map-wrap #map-zoom-out:hover {
  background-color: #111860;
}

/* contact form
 * ----------------------------------------------- */
#cForm {
  margin-top: 3.6rem;
}

#cForm input, #cForm textarea, #cForm select {
  margin-bottom: 0;
}

#cForm input[type="submit"], #cForm .btn {
  margin-top: 6rem;
}

