/* ===================================================================
 * # imports 
 *
 *  */
 @import url("font-awesome/css/fontawesome-all.css");
 
 
 /* ===================================================================
  * # normalize
  * normalize.css v5.0.0 | MIT License |
  * github.com/necolas/normalize.css
  *
  *  */
 html {
   font-family: sans-serif;
   line-height: 1.15;
   -ms-text-size-adjust: 100%;
   -webkit-text-size-adjust: 100%;
 }

 
 
 body {
   margin: 0;
 }
 
 article, aside, footer, header, nav, section {
   display: block;
 }
 
 h1 {
   font-size: 2em;
   margin: 0.67em 0;
 }
 
 figcaption, figure, main {
   display: block;
 }
 
 figure {
   margin: 1em 40px;
 }
 
 hr {
   box-sizing: content-box;
   height: 0;
   overflow: visible;
 }
 
 pre {
   font-family: monospace, monospace;
   font-size: 1em;
   margin-top: 20px;
 }
 
 a {
   background-color: transparent;
   -webkit-text-decoration-skip: objects;
 }
 
 a:active, a:hover {
   outline-width: 0;
 }
 
 abbr[title] {
   border-bottom: none;
   text-decoration: underline;
   text-decoration: underline dotted;
 }
 
 b, strong {
   font-weight: inherit;
 }
 
 b, strong {
   font-weight: bolder;
 }
 
 code, kbd, samp {
   font-family: monospace, monospace;
   font-size: 1em;
 }
 
 dfn {
   font-style: italic;
 }
 
 mark {
   background-color: #ff0;
   color: #000;
 }
 
 small {
   font-size: 80%;
 }
 
 sub, sup {
   font-size: 75%;
   line-height: 0;
   position: relative;
   vertical-align: baseline;
 }
 
 sub {
   bottom: -0.25em;
 }
 
 sup {
   top: -0.5em;
 }
 
 audio, video {
   display: inline-block;
 }
 
 audio:not([controls]) {
   display: none;
   height: 0;
 }
 
 img {
   border-style: none;
 }
 
 svg:not(:root) {
   overflow: hidden;
 }
 
 button, input, optgroup, select, textarea {
   font-family: sans-serif;
   font-size: 100%;
   line-height: 1.15;
   margin: 0;
 }
 
 button, input {
   overflow: visible;
 }
 
 button, select {
   text-transform: none;
 }
 
 button, html [type="button"], [type="reset"], [type="submit"] {
   -webkit-appearance: button;
 }
 
 button::MozFocusInner, [type="button"]::MozFocusInner, [type="reset"]::MozFocusInner, [type="submit"]::MozFocusInner {
   border-style: none;
   padding: 0;
 }
 
 button:MozFocusring, [type="button"]:MozFocusring, [type="reset"]:MozFocusring, [type="submit"]:MozFocusring {
   outline: 1px dotted ButtonText;
 }
 
 fieldset {
   border: 1px solid #c0c0c0;
   margin: 0 2px;
   padding: 0.35em 0.625em 0.75em;
 }
 
 legend {
   box-sizing: border-box;
   color: inherit;
   display: table;
   max-width: 100%;
   padding: 0;
   white-space: normal;
 }
 
 progress {
   display: inline-block;
   vertical-align: baseline;
 }
 
 textarea {
   overflow: auto;
 }
 
 [type="checkbox"], [type="radio"] {
   box-sizing: border-box;
   padding: 0;
 }
 
 [type="number"]::WebkitInnerSpinButton, [type="number"]::WebkitOuterSpinButton {
   height: auto;
 }
 
 [type="search"] {
   -webkit-appearance: textfield;
   outline-offset: -2px;
 }
 
 [type="search"]::WebkitSearchCancelButton, [type="search"]::WebkitSearchDecoration {
   -webkit-appearance: none;
 }
 
 ::WebkitFileUploadButton {
   -webkit-appearance: button;
   font: inherit;
 }
 
 details, menu {
   display: block;
 }
 
 summary {
   display: list-item;
 }
 
 canvas {
   display: inline-block;
 }
 
 template {
   display: none;
 }
 
 [hidden] {
   display: none;
 }
 
 
 /* ===================================================================
  * # basic/base setup styles
  *
  *  */
 html {
   font-size: 62.5%;
   box-sizing: border-box;
 }
 
 *, *::before, *::after {
   box-sizing: inherit;
 }
 
 body {
   font-weight: normal;
   line-height: 1;
   word-wrap: break-word;
   text-rendering: optimizeLegibility;
   -webkit-overflow-scrolling: touch;
   -webkit-text-size-adjust: none;
 }
 
 body, input, button {
   -moz-osx-font-smoothing: grayscale;
   -webkit-font-smoothing: antialiased;
 }
 
 /*  
  * ## Media
  *  */
 img, video {
   max-width: 100%;
   height: auto;
 }
 
 /*  
  * ## Typography resets 
  *  */
 div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, form, p, blockquote, th, td {
   margin: 0;
   padding: 0;
 }
 
 h1, h2, h3, h4, h5, h6 {
   -webkit-font-smoothing: auto;
   -webkit-font-smoothing: antialiased;
   -webkit-font-variant-ligatures: common-ligatures;
   -moz-font-variant-ligatures: common-ligatures;
   font-variant-ligatures: common-ligatures;
   text-rendering: optimizeLegibility;
 }
 
 em, i {
   font-style: italic;
   line-height: inherit;
 }
 
 strong, b {
   font-weight: bold;
   line-height: inherit;
 }
 
 small {
   font-size: 60%;
   line-height: inherit;
 }
 
 ol, ul {
   list-style: none;
 }
 
 li {
   display: block;
 }
 
 /*  
  * ## links
  *  */
 a {
   text-decoration: none;
   line-height: inherit;
   color: #888;
 }
 
 a img {
   border: none;
 }
 
 /*  
  * ## inputs
  *  */
 fieldset {
   margin: 0;
   padding: 0;
 }
 
 input[type="email"],
 input[type="number"],
 input[type="search"],
 input[type="text"],
 input[type="tel"],
 input[type="url"],
 input[type="password"],
 textarea {
   -webkit-appearance: none;
   -moz-appearance: none;
   -ms-appearance: none;
   -o-appearance: none;
   appearance: none;
 }
 
 
 /* ===================================================================
  * # grid
  *
  *  */
 .row {
   width: 89%;
   max-width: 1200px;
   margin: 0 auto;
 }
 
 .row:after {
   content: "";
   display: table;
   clear: both;
 }
 
 .row .row {
   width: auto;
   max-width: none;
   margin-left: -20px;
   margin-right: -20px;
 }
 
 /* column blocks
  *  */
 [class*="col"] {
   float: left;
   padding: 0 20px;
 }
 
 [class*="col"] + [Class*="col"].End {
   float: right;
 }
 
 .collapse > [class*="col"], [Class*="col"].Collapse {
   padding: 0;
 }
 
 /* column width classes 
  *  */
 .colOne {
   width: 8.33333%;
 }
 
 .colTwo, .col16 {
   width: 16.66667%;
 }
 
 .colThree, .col14 {
   width: 25%;
 }
 
 .colFour, .col13 {
   width: 33.33333%;
 }
 
 .colFive {
   width: 41.66667%;
 }
 
 .colSix, .col12 {
   width: 50%;
 }
 
 .colSeven {
   width: 58.33333%;
 }
 
 .colEight, .col23 {
   width: 66.66667%;
 }
 
 .colNine, .col34 {
   width: 75%;
 }
 
 .colTen, .col56 {
   width: 83.33333%;
 }
 
 .colEleven {
   width: 91.66667%;
 }
 
 .colTwelve, .colFull {
   width: 100%;
 }
 
 /*  
  * ## medium size devices
  *  */
 @media only screen and (max-width:1200px) {
   .row .row {
     margin-left: -15px;
     margin-right: -15px;
   }
 
   [class*="col"] {
     padding: 0 15px;
   }
 
   .mdTwo, .md16 {
     width: 16.66667%;
   }
 
   .mdOne {
     width: 8.33333%;
   }
 
   .mdThree, .md14 {
     width: 25%;
   }
 
   .mdFour, .md13 {
     width: 33.33333%;
   }
 
   .mdFive {
     width: 41.66667%;
   }
 
   .mdSix, .md12 {
     width: 50%;
   }
 
   .mdSeven {
     width: 58.33333%;
   }
 
   .mdEight, .md23 {
     width: 66.66667%;
   }
 
   .mdNine, .md34 {
     width: 75%;
   }
 
   .mdTen, .md56 {
     width: 83.33333%;
   }
 
   .mdEleven {
     width: 91.66667%;
   }
 
   .mdTwelve, .mdFull {
     width: 100%;
   }
 
 }
 
 /*  
  * ## tablets
  *  */
 @media only screen and (max-width:800px) {
   .row {
     width: 90%;
   }
 
   .tab14 {
     width: 25%;
   }
 
   .tab13 {
     width: 33.33333%;
   }
 
   .tab12 {
     width: 50%;
   }
 
   .tab23 {
     width: 66.66667%;
   }
 
   .tab34 {
     width: 75%;
   }
 
   .tabFull {
     width: 100%;
   }
 
   .hideOnTablet {
     display: none;
   }
 
 }
 
 /*  
  * ## mobile devices
  *  */
 @media only screen and (max-width:600px) {
   .row {
     width: auto;
   }
 
   .row .row {
     margin-left: -10px;
     margin-right: -10px;
     padding-left: 0;
     padding-right: 0;
   }
 
   [class*="col"] {
     padding: 0 10px;
   }
 
   .mob14 {
     width: 25%;
   }
 
   .mob13 {
     width: 33.33333%;
   }
 
   .mob12 {
     width: 50%;
   }
 
   .mob23 {
     width: 66.66667%;
   }
 
   .mob34 {
     width: 75%;
   }
 
   .mobFull {
     width: 100%;
   }
 
   .hideOnMobile {
     display: none;
   }
 
 }
 
 /*  
  * ## small mobile devices
  *  */
 
 /* stack columns on small mobile devices
  *  */
 @media only screen and (max-width:400px) {
   .row .row {
     margin-left: 0;
     margin-right: 0;
   }
 
   [class*="col"]:Not(.colBlock) {
     width: 100% !important;
     float: none !important;
     clear: both !important;
     margin-left: 0;
     margin-right: 0;
     padding: 0;
   }
 
   [class*="col"] + [Class*="col"].End {
     float: none;
   }
 
   .stack .colBlock {
     width: 100% !important;
     float: none !important;
     clear: both !important;
     margin-left: 0;
     margin-right: 0;
   }
 
 }
 
 @media only screen and (max-width:340px) {
   .row {
     padding-left: 20px;
     padding-right: 20px;
   }
 
 }
 
 
 /* ===================================================================
  * # block grids
  *  */
 
 /* EquallySized columns define at row level
  *  */
 [class*="block"]:After {
   content: "";
   display: table;
   clear: both;
 }
 
 .block16 .ColBlock {
   width: 16.66667%;
 }
 
 .block15 .ColBlock {
   width: 20%;
 }
 
 .block14 .ColBlock {
   width: 25%;
 }
 
 .block13 .ColBlock {
   width: 33.33333%;
 }
 
 .block12 .ColBlock {
   width: 50%;
 }
 
 /**
  * Clearing for block grid columns. Allow columns with 
  * different heights to align properly.
  */
 .block16 .ColBlock:nthChild(6n+1),
 .block15 .ColBlock:nthChild(5n+1),
 .block14 .ColBlock:nthChild(4n+1),
 .block13 .ColBlock:nthChild(3n+1),
 .block12 .ColBlock:nthChild(2n+1) {
   clear: both;
 }
 
 /*  
  * ## medium size devices
  *  */
 @media only screen and (max-width:1200px) {
   .blockM16 .ColBlock {
     width: 16.66667%;
   }
 
   .blockM15 .ColBlock {
     width: 20%;
   }
 
   .blockM14 .ColBlock {
     width: 25%;
   }
 
   .blockM13 .ColBlock {
     width: 33.33333%;
   }
 
   .blockM12 .ColBlock {
     width: 50%;
   }
 
   .blockMFull .colBlock {
     width: 100%;
     clear: both;
   }
 
   [class*="blockM"] .ColBlock:nthChild(n) {
     clear: none;
   }
 
   .blockM16 .ColBlock:nthChild(6n+1),
   .blockM15 .ColBlock:nthChild(5n+1),
   .blockM14 .ColBlock:nthChild(4n+1),
   .blockM13 .ColBlock:nthChild(3n+1),
   .blockM12 .ColBlock:nthChild(2n+1) {
     clear: both;
   }
 
 }
 
 /*  
