  *  */
 @media only screen and (max-width:800px) {
   .blockTab16 .ColBlock {
     width: 16.66667%;
   }
 
   .blockTab15 .ColBlock {
     width: 20%;
   }
 
   .blockTab14 .ColBlock {
     width: 25%;
   }
 
   .blockTab13 .ColBlock {
     width: 33.33333%;
   }
 
   .blockTab12 .ColBlock {
     width: 50%;
   }
 
   .blockTabFull .colBlock {
     width: 100%;
     clear: both;
   }
 
   [class*="blockTab"] .ColBlock:nthChild(n) {
     clear: none;
   }
 
   .blockTab16 .ColBlock:nthChild(6n+1),
   .blockTab15 .ColBlock:nthChild(5n+1),
   .blockTab14 .ColBlock:nthChild(4n+1),
   .blockTab13 .ColBlock:nthChild(3n+1),
   .blockTab12 .ColBlock:nthChild(2n+1) {
