/* ===================================================================
 * # base style overrides
 *
 *  */
html {
    font-size: 12px;
  }
  
  
  html, body {
    height: 100%;
  }
  
  body {
    font-family: Montserrat;
    font-size: 1.8rem;
    font-style: normal;
    font-weight: normal;
    line-height: 1.8333;
    color: rgba(0, 0, 0, 0.8);
    margin: 0;
    padding: 0;
  }
  
  /*  
   * ## links
   *  */
  a {
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  
  a:hover, a:focus, a:active {
    color: #7b287d;
  }
  
  a:hover, a:active {
    outline: 0;
  }
  
  
  /* ===================================================================
   * # typography & general theme styles
   * 
   *  */
  h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
    font-weight: 700;
    color: #000000;
    text-rendering: optimizeLegibility;
  }
  
  h1, .h1, h2, .h2, h3, .h3, h4, .h4 {
    padding-top: 90px;
    margin-bottom: 1.8rem;
  }
  
  @media only screen and (max-width:600px) {
    h1, .h1, h2, .h2, h3, .h3, h4, .h4 {
      margin-top: 5.1rem;
      padding: 0px;
    }
  
  }
  
  h5, .h5, h6, .h6 {
    margin-top: 4.2rem;
    margin-bottom: 1.5rem;
  }
  
  @media only screen and (max-width:600px) {
    h5, .h5, h6, .h6 {
      margin-top: 3.6rem;
      margin-bottom: 0.9rem;
    }
  
  }

  p {
    margin: 30px 0px;
  }
  
  h1, .h1 {
    font-size: 3.6rem;
    line-height: 1.25;
    letter-spacing: -.1rem;
  }
  
  @media only screen and (max-width:600px) {
    h1, .h1 {
      font-size: 3.3rem;
      letter-spacing: -.07rem;
    }
  
  }
  
  h2, .h2 {
    font-size: 3rem;
    line-height: 1.3;
  }
  
  h3, .h3 {
    font-size: 2.4rem;
    line-height: 1.25;
  }
  
  h4, .h4 {
    font-size: 2.1rem;
    line-height: 1.286;
  }
  
  h5, .h5 {
    font-size: 1.8rem;
    line-height: 1.333;
  }
  
  h6, .h6 {
    font-size: 1.6rem;
    line-height: 1.3125;
    text-transform: uppercase;
    letter-spacing: .16rem;
  }
  
  p img {
    margin: 0;
  }
  
  p.lead {
    font-family: "Nunito Sans", sans-serif;
    font-weight: 300;
    font-size: 2.7rem;
    line-height: 1.667;
    margin-bottom: 3.6rem;
    color: #000000;
  }
  
  @media only screen and (max-width:1200px) {
    p.lead {
      font-size: 2.6rem;
    }
  
  }
  
  @media only screen and (max-width:1000px) {
    p.lead {
      font-size: 2.5rem;
    }
  
  }
  
  @media only screen and (max-width:800px) {
    p.lead {
      font-size: 2.2rem;
    }
  
  }
  
  @media only screen and (max-width:400px) {
    p.lead {
      font-size: 2.1rem;
    }
  
  }
  
  em, i, strong, b {
    font-size: inherit;
    line-height: inherit;
  }
  
  em, i {
    font-family: "Nunito Sans", sans-serif;
    font-style: italic;
  }
  
  strong, b {
    font-family: "Nunito Sans", sans-serif;
    font-weight: 700;
  }
  
  small {
    font-size: 1.2rem;
    line-height: inherit;
  }
  
  blockquote {
    margin: 6rem 0;
    padding: 0 6rem;
    position: relative;
  }
  
  blockquote::before {
    content: "\201C";
    font-size: 28rem;
    line-height: 0;
    margin: 0;
    color: rgba(0, 0, 0, 0.12);
    font-family: "Nunito Sans", sans-serif;
    font-weight: 400;
    position: absolute;
    top: 10.2rem;
    left: -1.2rem;
  }
  
  blockquote p {
    font-family: "Nunito Sans", sans-serif;
    padding: 0;
    font-size: 2.7rem;
    line-height: 1.667;
    color: #000000;
  }
  
  blockquote cite {
    display: block;
    font-family: "Nunito Sans", sans-serif;
    font-size: 1.5rem;
    font-style: normal;
    line-height: 1.6;
    color: rgba(0, 0, 0, 0.5);
  }
  
  blockquote cite::before {
    content: "\2014 \0020";
  }
  
  blockquote cite a, blockquote cite a:visited {
    color: rgba(0, 0, 0, 0.5);
    border: none;
  }
  
  @media only screen and (max-width:1200px) {
    blockquote {
      padding: 0 0 0 6rem;
    }
  
    blockquote p {
      font-size: 2.6rem;
    }
  
  }
  
  @media only screen and (max-width:1000px) {
    blockquote p {
      font-size: 2.5rem;
    }
  
  }
  
  @media only screen and (max-width:800px) {
    blockquote {
      padding: 0 0 0 3rem;
    }
  
    blockquote p {
      font-size: 2.5rem;
    }
  
  }
  
  @media only screen and (max-width:400px) {
    blockquote p {
      font-size: 2.2rem;
    }
  
  }
  
  abbr {
    font-family: "Nunito Sans", sans-serif;
    font-weight: 700;
    font-variant: small-caps;
    text-transform: lowercase;
    letter-spacing: .05rem;
    color: rgba(0, 0, 0, 0.5);
  }
  
  var, kbd, samp, code, pre {
    font-family: Consolas, "Andale Mono", Courier, "Courier New", monospace;
