  /*  
   * ## additional typo styles 
   *  */
  
  /* drop cap 
   *  */
  .dropCap:firstLetter {
    float: left;
    margin: 0;
    padding: 1.5rem .6rem 0 0;
    font-family: "Libre Baskerville", serif;
    font-weight: 700;
    font-size: 8.4rem;
    line-height: 6rem;
    text-indent: 0;
    background: transparent;
    color: #000000;
  }
  
  /* line definition style 
   *  */
  .lining dt, .lining dd {
    display: inline;
    margin: 0;
  }
  
  .lining dt + dt:before, .lining dd + dt:before {
    content: "\A";
    white-space: pre;
  }
  
  .lining dd + dd:before {
    content: ", ";
  }
  
  .lining dd + dd:before {
    content: ", ";
  }
  
  .lining dd:before {
    content: ": ";
    margin-left: -0.2em;
  }
  
  /* dictionary definition style 
   *  */
  .dictionaryStyle dt {
    display: inline;
    counter-reset: definitions;
  }
  
  .dictionaryStyle dt + dt:before {
    content: ", ";
    margin-left: -0.2em;
  }
  
  .dictionaryStyle dd {
    display: block;
    counter-increment: definitions;
  }
  
  .dictionaryStyle dd:before {
    content: counter(definitions, decimal) ". ";
  }
  
  /** 
   * Pull Quotes
   * 
   * markup:
   *
   * <aside class="pullQuote">
   *    <blockquote>
   *      <p></p>
   *    </blockquote>
   *  </aside>
   *
   *  */
  .pullQuote {
    position: relative;
    padding: 2.1rem 3rem 2.1rem 0px;
  }
  
  .pullQuote:before, .pullQuote:after {
    height: 1em;
    position: absolute;
    font-size: 10rem;
    font-family: Arial, Sans-Serif;
    color: rgba(0, 0, 0, 0.25);
  }
  
  .pullQuote:before {
    content: "\201C";
    top: -3.6rem;
    left: 0;
  }
  
  .pullQuote:after {
    content: '\201D';
    bottom: 3.6rem;
    right: 0;
  }
  
  .pullQuote blockquote {
    margin: 0;
    padding-left: 4.5rem;
    padding-right: 0;
    position: relative;
  }
  
  .pullQuote blockquote p {
