    margin-bottom: 1.8rem;
    color: white;
    border: none;
  }
  
  .sFooterSubscribe #mcForm input[type="email"]::WebkitInputPlaceholder {
  
    /* WebKit, Blink, Edge */
    color: #FFFFFF;
  }
  
  .sFooterSubscribe #mcForm input[type="email"]:MozPlaceholder {
  
    /* Mozilla Firefox 4 to 18 */
    color: #FFFFFF;
    opacity: 1;
  }
  
  .sFooterSubscribe #mcForm input[type="email"]::MozPlaceholder {
  
    /* Mozilla Firefox 19+ */
    color: #FFFFFF;
    opacity: 1;
  }
  
  .sFooterSubscribe #mcForm input[type="email"]:MsInputPlaceholder {
  
    /* Internet Explorer 1011 */
    color: #FFFFFF;
  }
  
  .sFooterSubscribe #mcForm input[type="email"]::MsInputPlaceholder {
  
    /* Microsoft Edge */
    color: #FFFFFF;
  }
  
  .sFooterSubscribe #mcForm input[type="email"]::placeholder {
  
    /* Most modern browsers support this now. */
    color: #FFFFFF;
  }
  
  .sFooterSubscribe #mcForm input[type="email"].placeholder {
    color: #FFFFFF !important;
  }
  
