      margin-top: 1.2rem;
    }
  
    .sContentEntryNav .sContentNav {
      max-width: 460px;
    }
  
    .sContentNav span {
      font-size: 1.35rem;
    }
  
    .sContentNav a {
      font-size: 2.8rem;
    }
  
  }
  
  @media only screen and (max-width:500px) {
    .sContentEntryNav {
      margin-top: 9rem;
    }
  
    .sContentEntryNav .sContentNav {
      max-width: 400px;
    }
  
    .sContentNav a {
      font-size: 2.6rem;
    }
  
  }
  
  @media only screen and (max-width:400px) {
    .entryMedia, .entryHeader {
      padding-left: 0;
      padding-right: 0;
    }
  
    .entryAuthor {
      padding: 3.6rem 2.5rem;
    }
  
    .sContentNav a {
      font-size: 2.4rem;
    }
  
  }
  
  /* 
   * ## comments 
   *  */
  .commentsWrap {
    padding: 1rem;
  }
  
  .commentsWrap h3 {
    font-weight: 700;
    font-size: 3rem;
    line-height: 1.5;
    text-align: center;
    margin-top: 0;
    margin-bottom: 1.5rem;
  }
  
  .commentsWrap h3 span {
    display: block;
    font-weight: 400;
    font-size: 1.8rem;
    line-height: 1.667;
    color: rgba(0, 0, 0, 0.5);
  }
  
  /* comments
   *  */
  #comments {

    margin: 8rem;
    padding-top: 1.2rem;
    padding-bottom: 1.2rem;
    margin: 8rem;
    max-width: 900px;
  }
  
  #comments ol, #comments ul {
    list-style: none;
  }
  
  .commentlist {
    margin: 6rem 0 5.4rem;
    padding: 0;
  }
  
  .commentlist > .comment {
    position: relative;
    list-style: none;
    margin: 0;
    padding: 1.8rem 0 0 0;
    padding-left: 14%;
  }
  
  .commentAvatar {
    margin-right: 20px;
    left: 0;
    display: block;
  }
  
  .commentAvatar img {
    height: 6.6rem;
    width: 6.6rem;
    border-radius: 100%;
    vertical-align: bottom;
  }
  
  .commentInfo {
    position: relative;
  }
  
  .commentInfo .commentAuthor {
    font-weight: 700;
    font-size: 1.9rem;
    line-height: 1.579;
    font-style: normal;
    color: #000000;
  }
  
  .commentMeta {
    font-family: "Nunito Sans", sans-serif;
    font-style: normal;
    display: block;
    color: rgba(0, 0, 0, 0.5);
    text-align: right;
    width: 150px;
    position: absolute;
    top: 0;
    right: 0;
  }
  
  .commentMeta .commentTime {
    font-size: 1.4rem;
    text-transform: uppercase;
    letter-spacing: .1rem;
    color: rgba(0, 0, 0, 0.5);
  }
  
  .commentMeta .commentReply a {
    font-size: 1.3rem;
    font-family: "Nunito Sans", sans-serif;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: .2rem;
    color: #000000;
  }
  
  .commentText {
    clear: both;
    margin: 1.5rem 0 0 0;
    padding: 0 175px 0 0;
  }
  
  .comment ul.children {
    margin: 0;
    padding: 0;
  }
  
  .comment ul.children li {
    padding-left: 5%;
    margin-top: calc(3rem + 1.8rem);
    border-left: 1px solid rgba(0, 0, 0, 0.15);
  }
  
  /* comments form
   *  */
  .commentsWrap .commentRespond {
    max-width: 800px;
  }
  
  .commentRespond form input,
  .commentRespond form textarea,
  .commentRespond form select {
    margin-bottom: 0;
  }
  
  .commentRespond form input[type="submit"],
  .commentRespond form .btn {
    margin-top: 6rem;
  }
  
  /*  
   * responsive:
   * comments
   *  */
  @media only screen and (max-width:1000px) {
    #comments, .commentsWrap .commentRespond {
      max-width: 750px;
    }
  
    .commentAvatar img {
      height: 6rem;
      width: 6rem;
    }
  
    .commentMeta {
      text-align: left;
      width: auto;
      position: static;
    }
  
    .commentMeta .commentTime, .commentMeta .commentReply {
      display: inline-block;
    }
  
    .commentMeta .commentTime {
      margin-right: 12px;
    }
  
    .commentText {
      padding: 0;
    }
  
    .commentsWrap .commentRespond {
      margin-top: 4.5rem;
    }
  
  }
  
  @media only screen and (max-width:800px) {
    .commentAvatar img {
      margin-top: 0;
      height: 5.1rem;
      width: 5.1rem;
    }
  
  }
  
  @media only screen and (max-width:600px) {
    .commentsWrap h3 {
      font-size: 2.8rem;
    }
  
    .commentsWrap h3 span {
      font-size: 1.6rem;
    }
  
    .commentlist > .comment {
      padding-top: 0;
      padding-left: 0;
    }
  
    .commentAvatar {
      display: none;
    }
  
    .comment ul.children li {
      margin-top: calc(3rem + 0rem);
    }
  
  }
  
  @media only screen and (max-width:500px) {
    .comment ul.children li {
      border-left: none;
    }
  
  }
  
  @media only screen and (max-width:400px) {
    .commentsWrap {
      margin-top: 3rem;
    }
  
  }
  
  
  /* ===================================================================
   * # page styles and page components
   *
   *  */
  .sContentNarrow h4.removeTopMargin {
    margin-top: 0;
  }
  
  .sContentNarrow h4.quarterTopMargin {
    margin-top: 1.5rem;
  }
  
  .sContentNarrow h4.halfTopMargin {
    margin-top: 3rem;
  }
  
  /*  
   * ## contact page
   *  */
  
  /* map
   *  */
  #mapWrap {
    background: #FFFFFF;
    z-index: 500;
    height: 420px;
    margin: 6rem 0 3rem 0;
  }
  
  #mapWrap .gmStyle img {
    max-width: none;
  }
  
  #mapWrap .gmStyle label {
    width: auto;
    display: inline;
  }
  
  #mapWrap #mapContainer {
    height: 100%;
    width: 100%;
    background: #DDDDDD;
    position: relative;
  }
  
  #mapWrap #mapZoomIn, #mapWrap #mapZoomOut {
    height: 3.2rem;
    width: 3.2rem;
    cursor: pointer;
    margin-right: 4rem;
    color: white;
    background-color: #000000;
    background-repeat: no-repeat;
    background-position: center center;
    display: none;
  }
  
  #mapWrap #mapZoomIn {
    margin-bottom: 2px;
    margin-top: 30px;
  }
  
  #mapWrap #mapZoomIn {
    background-image: url(../img/icons/icon-plus.svg);
    background-size: 10px 10px;
  }
  
  #mapWrap #mapZoomOut {
    background-image: url(../img/icons/icon-minus.svg);
    background-size: 10px 10px;
  }
  
  #mapWrap #mapZoomIn:hover, #mapWrap #mapZoomOut:hover {
    background-color: #111860;
  }
  
  /* contact form
   *  */
  #cForm {
    margin-top: 3.6rem;
  }
  
  #cForm input, #cForm textarea, #cForm select {
    margin-bottom: 0;
  }
  
  #cForm input[type="submit"], #cForm .btn {
    margin-top: 6rem;
  }
  
